@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDekstop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin onDekstopXL {
  @media (min-width: $dekstop-large-min-width) {
    @content;
  }
}

@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet() {
    --columns: 6;
    column-gap: 30px;
  }

  @include onDekstop() {
    --columns: 12;
    column-gap: 20px;
  }
}

@mixin paddingInline {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 27px;
  }

  @include onDekstop {
    padding-inline: 50px;
  }

  @include onDekstopXL {
    max-width: 1200px;
    margin-inline: auto;
    padding-inline: 130px;
  }
}

@mixin scalePhoto($_property) {
  transition: all 0.4s;

  &:hover {
    transform: scale($_property);
    border-radius: 20px;
  }
}
